<template>
  <div>
    <Card style="margin:10px;">
      <Row>
        <Col style="display: flex; justify-content: flex-start;">
          <div>
            <span style="margin-left:18px;font-size:14px;font-family:Microsoft YaHei;font-weight:400;color:rgba(255,255,255,1);">操作类型</span>
            <Select v-model="operateType" clearable :style="{width:200 * base + 'px','margin-left':'9px'}">
              <Option v-for="item in operateTypeList" :value="item.metaDataValue" :key="item.metaDataValue">{{ item.metaDataName }}</Option>
            </Select>
          </div>
          <div style="margin-left:50px">
            <span style="font-size:14px;font-family:Microsoft YaHei;font-weight:400;color:rgba(255,255,255,1);">日期</span>
            <DatePicker 
              type="date" 
              placeholder="请选择日期" 
              :style="{width:200 * base + 'px','margin-left':'9px'}"
              @on-change="dataChange">
            </DatePicker>
          </div>
          <div>
            <span style="margin: 15px 30px;">
              <Button type="primary" @click="search">查询</Button>
            </span>
          </div>
        </Col>
      </Row>
    </Card>
    <Card style="margin:10px;">
      <div style="margin-top:10px;">
        <Table :columns="columns1" :data="data1" :loading="loadingStatus" :height="tabHeight"></Table>
        <Page 
          :total="totalCount" 
          :current="pageNo" 
          show-sizer 
          show-total 
          show-elevator 
          @on-change="pageChange" 
          @on-page-size-change="pageSizeChange" 
          placement="top" 
          style="text-align:right;margin:20px 0"/>
      </div>
    </Card>
  </div>
</template>
<script>
import api from '@/api/api'
export default {
  data () {
    return {
      loadingStatus:false,
      operateType: 10000, // 操作日志：按操作类型查询
      operateTypeList: [],
      // 操作日志表格数据
      columns1: [
        {
          title: '操作类型',
          key: 'operateName'
        },
        {
          title: '描述',
          key: 'operateDesc'
        },
        {
          title: '操作员ID',
          key: 'operateUserid'
        },
        {
          title: '操作员名称',
          key: 'operateUsername'
        },
        {
          title: 'IP',
          key: 'operateIp'
        },
        {
          title: '日期',
          key: 'operateDate',
          // align: 'center'
        }
      ],
      data1: [],
      dateData:'',
      //======分页
      totalCount:0,
      pageNo:1,
      pageSize:10,

      base:1,   // 分辨率自适应 计算后的倍数
      tabHeight:0,  // 分辨率自适应调整：表格的高度
    }   
  }, 
  created(){
    this.operateTypeList = JSON.parse(localStorage.getItem('selectAllData'))[0].metaDictList;
    this.operateTypeList.unshift({metaDataValue:10000,metaDataName:'全部'});
    this.operateLogSearch();
  },
  mounted(){
    this.autoFix();
  },
  methods:{

    /** 自适应 */
    autoFix(){
      let vm = this;
      this.getDom();
      window.onresize = function(){
        vm.getDom();
      }
    },

    /** 获取窗口的宽和高 */
    getDom(){
      let width = window.innerWidth;
      let height = window.innerHeight;
      this.base = (width-160)/(1024-160);
      // this.tabHeight = height - 350;  // 表格页面有“过滤搜索条件”card，表格上方有Button，则采用该方式计算。
      this.tabHeight = height - 325;  // 表格页面有“过滤搜索条件”card，表格上方无Button，则采用该方式计算。
      // this.tabHeight = height - 275;  // 表格页面无 “过滤搜索条件”card,但表格上方有Button，则采用该方式计算。
      // this.tabHeight = height - 250;   // 表格页面无 “过滤搜索条件”card,表格上方无Button，则采用该方式计算。
    },

    //日期切换
    dataChange(val){
      this.dateData = val;
    },

    search(){
      this.pageNo = 1;
      this.operateLogSearch();
    },

    operateLogSearch(){
      let param = {
        operatorType:this.operateType ===10000?'':this.operateType,
        date:this.dateData,
        pageNo:this.pageNo,
        pageSize:this.pageSize
      }
      this.loadingStatus = true
      api.searchOperatorLog(param).then(res=>{
        console.log('日志',res)
        this.data1 = res.result
        this.totalCount = res.count
        this.loadingStatus = false
      },err=>{
        this.loadingStatus = false
      })
    },

    pageChange(page){
      this.pageNo = page
      this.operateLogSearch()
    },
    
    pageSizeChange(size){
      this.pageSize = size
      if(this.pageNo === 1){
        this.operateLogSearch()
      }
    },
  },
}
</script>
