<template>
  <div>
    <div
      style="width:100%;
          height:38px;
          background:rgba(13,67,111,1);
          box-shadow:0px 2px 10px 0px rgba(0, 0, 0, 0.05);
          font-size:14px;
          font-family:Microsoft YaHei;
          font-weight:400;
          color:rgba(255,255,255,1);
          padding-left:24px;
          line-height:38px;"
    >
      <Row>
        <Col span="24">
          <Row>
            <Col span="14">
              系统日志
            </Col>
            <Col span="10">
              <Row
                type="flex"
                justify="end"
              >
                <div
                  @click="click"
                  id="startLog"
                  v-bind:class="[ target.id === 'startLog' ? 'active1': '',]"
                  style="width:100px;text-align:center;cursor:pointer;"
                  
                >
                  系统启动日志
                </div>
                <div
                  @click="click"
                  id="operateLog"
                  v-bind:class="[ target.id === 'operateLog' ? 'active1': '',]"
                  style="width:85px;text-align:center;cursor:pointer;"
                >
                  操作日志
                </div>
                <div
                  @click="click"
                  id="businessLog"
                  v-bind:class="[ target.id === 'businessLog' ? 'active1': '',]"
                  style="width:75px;text-align:center;cursor:pointer;"
                >
                  业务自检
                </div>
              </Row>
            </Col>
          </Row>
        </Col>
      </Row>
    </div>
    <div v-if="current === 1">
      <StartLog ref="startLog" />
    </div>
    <div v-if="current === 2">
      <OperateLog ref="operateLog" />
    </div>
    <div v-if="current === 3">
      <BusinessLog ref="businessLog" />
    </div>
  </div>
</template>

<script>
import StartLog from './components/start-log.vue';
import OperateLog from './components/operate-log.vue';
import BusinessLog from './components/business-log.vue';
export default {
  components:{
    StartLog,
    OperateLog,
    BusinessLog,
  },
  data(){
    return{
      target: { id: "startLog" }, // 系统启动日志
      current:1, // 当前显示的是 系统启动日志
    }
  },
  created() {

  },
  methods:{
    click(ev) {
      this.target = ev.target;
      // console.log(this.target.id);
      let Id = this.target.id;
      if (Id === "startLog") {
        this.current = 1;
      } else if (Id === "operateLog") {
        this.current = 2;

      } else {
        this.current = 3;
      }
    }
  }
}
</script>
<style>
.active1 {
  color: #02E8FF;
  border-bottom: 2px solid #02E8FF;
}
</style>
