<template>
  <div>
    <Card style="margin:10px;">
      <Row>
        <Col style="display:flex; justify-content:flex-start;">
          <div>
            <span style="margin-left:20px;font-size:14px;font-family:Microsoft YaHei;font-weight:400;color:rgba(255,255,255,1);">系统模块名称</span>
            <Input 
              v-model="systemModuleName"
              placeholder="请输入"
              :style="{width:180 * base + 'px',height:'32px','border-radius':'2px','margin-left':'9px'}"
            ></Input>
          </div>
          <div style="margin-left:30px">
            <span style="font-size:14px;font-family:Microsoft YaHei;font-weight:400;color:rgba(255,255,255,1);">日期</span>
            <DatePicker 
              format="yyyy-MM-dd" 
              type="date" 
              v-model="dateData" 
              @on-change="importDateChange" 
              placeholder="请选择日期"
              :style="{width:200 * base + 'px','margin-left':'9px'}" 
            ></DatePicker>
          </div>
          <div>
            <span style="margin: 15px 30px;">
              <Button type="primary" @click="changePage(1)">查询</Button>
            </span>
          </div>
        </Col>
      </Row>
    </Card>
    <Card style="margin:10px;">
      <div style="margin-top:10px;">
        <Table :columns="columns1" :data="data1" :loading="loadingStatus" :height="tabHeight"></Table>
        <Page 
          :total="totalCount" 
          :current="pageNo" 
          @on-change="changePage" 
          @on-page-size-change="changePageSize" 
          show-sizer 
          show-elevator 
          show-total 
          placement='top' 
          style="text-align:right;margin:20px 0"
        />
      </div>
    </Card>
  </div>
</template>
<script>
import api from '@/api/api';

export default {
  data () {
    return {
      columns1: [
        {
          title: '系统模块名称',
          key: 'name'
        },
        {
          title: '描述',
          key: 'moduleDescription'
        },
        {
          title: '启动状态',
          key: 'status'
        },
        {
          title: '创建时间',
          key: 'createAt'
        }
      ],
      data1: [],
      dateData:'', // 输入的日期数据
      loadingStatus:false,
      logType:0, // 日志类型：0、启动日志；1为业务日志
      systemModuleName:'', // 系统模块名称

      // 分页
      current:1,
      totalCount:0,
      pageNo:1,
      pageSize:10,

      base:1,   // 分辨率自适应 计算后的倍数
      tabHeight:0,  // 分辨率自适应调整：表格的高度
    }
  },
  created() {
    this.init();
    this.queryStartLog();
  },
  mounted() {
    this.autoFix();
  },
  methods:{

    /** 自适应 */
    autoFix(){
      let vm = this;
      this.getDom();
      window.onresize = function(){
        vm.getDom();
      }
    },

    /** 获取窗口的宽和高 */
    getDom(){
      let width = window.innerWidth;
      let height = window.innerHeight;
      this.base = (width-160)/(1024-160);
      // this.tabHeight = height - 350;  // 表格页面有“过滤搜索条件”card，表格上方有Button，则采用该方式计算。
      this.tabHeight = height - 325;  // 表格页面有“过滤搜索条件”card，表格上方无Button，则采用该方式计算。
      // this.tabHeight = height - 275;  // 表格页面无 “过滤搜索条件”card,但表格上方有Button，则采用该方式计算。
      // this.tabHeight = height - 250;   // 表格页面无 “过滤搜索条件”card,表格上方无Button，则采用该方式计算。
    },

    init(){
      /** 渲染自定义组件 */
      let vm = this;
      this.columns1.forEach(item => {
        if(item.key === 'status') {
            item.render = (h, param) => {
              return h('div', [
                h(
                  'span',
                  {
                    props: {
                      type:'text',
                    },
                    style: {
                      color:param.row.status===0?'rgba(2,232,255,1)':'rgba(245,108,108,1)',
                    },
                  }, param.row.status === 0?'正常':'异常'
                )
              ])
            }
        }
      })
    },
    
    importDateChange(val){
      this.dateData = val;
    },
    
    /** 查询系统启动日志 */
    queryStartLog(){
      let param = {
        type:this.logType,
        date:this.dateData,
        name:this.systemModuleName,
        pageNo:this.pageNo,
        pageSize:this.pageSize,
      }
      this.loadingStatus = true;

      this.data1 = [];
      api.seacherDrbsSystemLogList(param).then(
        res => {
          // console.log(res);
          this.totalCount = res.count;
          res = res.result;
          this.loadingStatus = false;
          this.data1 = res;
          if(this.data1.length === 0 && this.pageNo > 1){
            this.pageNo--;
            this.queryStartLog();
          }
        }, err => {
          this.loadingStatus = false;
        }
      )        
    },

    // 改变页面
    changePage(Num) {
      this.pageNo = Num
      this.queryStartLog();
    },
    
    /** 改变页面条数 */
    changePageSize(size) {
      this.pageSize = size
      if (this.pageNo === 1) {
        this.queryStartLog();
      }
    },
  }
}
</script>